
import { homeApi } from "./conf/api";
import { categoryList } from "./js/nav-category";
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

export default {
components: {
Tab,
TabItem,
Swiper,
SwiperItem,
Sticky,
LoadMore,
Scroller
},
data() {
return {
pageIndex: 1, // 加载数据的页数
materialId: 3756, // 编号
categoryId: 0, // 类别
scrollerHeight: "",
action: "GetMaterialGoods", // 默认请求类型
commodityList: [], // 商品列表
tabShow: true, // 为true显示推荐布局,为false不显示
loadingShow: false, // 为true显示底部加载布局,为false不显示
loadingEnd: true, // 为true显示loading，为false不显示

tabWidth: "30px",

tabList: [
{
title: "推荐",
action: "GetMaterialGoods",
MaterialId: "3756",
CategoryId: ""
},
{
title: "女装",
action: "GetMaterialGoods",
MaterialId: "3767",
CategoryId: ""
},
{
title: "女鞋",
action: "UGetGoodsH5",
MaterialId: "",
CategoryId: "1805111645484568078531015154"
},
{
title: "母婴",
action: "GetMaterialGoods",
MaterialId: "3760",
CategoryId: ""
},
{
title: "美食",
action: "GetMaterialGoods",
MaterialId: "3761",
CategoryId: ""
},
{
title: "百货",
action: "GetMaterialGoods",
MaterialId: "3758",
CategoryId: ""
},
{
title: "美妆个护",
action: "GetMaterialGoods",
MaterialId: "3763",
CategoryId: ""
},
{
title: "儿童玩具",
action: "UGetGoodsH5",
MaterialId: "",
CategoryId: "1805122245106455071535657505"
},
{
title: "内衣装饰",
action: "GetMaterialGoods",
MaterialId: "3765",
CategoryId: ""
},
{
title: "男装",
action: "GetMaterialGoods",
MaterialId: "3764",
CategoryId: ""
},
{
title: "男鞋",
action: "UGetGoodsH5",
MaterialId: "",
CategoryId: "1805101547281188676554953495"
},
{
title: "宠物",
action: "UGetGoodsH5",
MaterialId: "",
CategoryId: "1805122236428205665525210048"
},
{
title: "家电",
action: "UGetGoodsH5",
MaterialId: "",
CategoryId: "1805122037134276562485310210"
},
{
title: "数码",
action: "GetMaterialGoods",
MaterialId: "3759",
CategoryId: ""
}
],

swiperList: [
require("../../assets/1.jpg"),
require("../../assets/2.jpg"),
require("../../assets/3.jpg")
],

prefectureList: []
};
},
mounted() {
this.init();
},

methods: {

init() {

this.setScrollerHeight();

this.prefectureList = categoryList[0].ChildList;

this.$vux.loading.show({ text: "加载中" });

this.requestGetMaterialGoods();
},
setScrollerHeight() {
this.scrollerHeight = window.innerHeight - 100 + "px";
},

onTabItemClick(item, index) {

var categoryChildList = categoryList;

if (categoryChildList[index].ChildList.length > 7) {
this.prefectureList = categoryChildList[index].ChildList.slice(0, 7);

var more = {
Id: "",
CategoryName: "查看更多",
SearchEnable: true,
PhotoUrl: require("../../assets/home_more.png"),
Level: 1,
ChildList: []
};

this.prefectureList.push(more);
} else {
this.prefectureList = categoryChildList[index].ChildList;
}

this.tabShow = index == 0 ? true : false;

this.tabWidth = item.title.length * 15 + "px";

this.action = item.action;

this.$vux.loading.show({ text: "加载中" });

if (this.action == "GetMaterialGoods") {
this.materialId = item.MaterialId;
} else {
this.categoryId = item.CategoryId;
}

this.onScrolldown();
},

prefectureItemClick(item) {
if (item.CategoryName == "查看更多") {

this.$parent.tabbarItemClick(1);
} else {

this.$router.push({
name: "commodityList",
params: {
item: item
}
});
}
},

onCommodityItemClick(item) {
this.$router.push({
name: "commoditydec",
params: {
item: item
}
});
},

onSwiperItemClick(index) {
alert("url" + index);
},

onScrolldown() {

this.pageIndex = 1;
if (this.action == "GetMaterialGoods") {
this.requestGetMaterialGoods();
} else {
this.requestUGetGoodsH5();
}
},

onScrollup() {
this.pageIndex = this.pageIndex + 1;
if (this.action == "GetMaterialGoods") {
this.requestGetMaterialGoods();
} else {
this.requestUGetGoodsH5();
}
},

requestUGetGoodsH5() {
this.$request
.loading(
this,
homeApi.UGetGoodsH5,
"post",
{
PromoterId: "",
CategoryId: this.categoryId,
Title: "",
PageSize: 20,
PageIndex: this.pageIndex,
platform: 2,
IsTmall: false,
HasCoupon: true,
NeedFreeShipment: false,
StartPrice: 0
},
""
)
.then(
success => {
this.loadingShow = true;
this.loadingEnd = success.data.code == 0 ? true : false;
if (success.data.code == 0) {
if (this.pageIndex == 1) {

this.commodityList = [];
}

this.commodityList = this.commodityList.concat(success.data.data);
}

if (this.pageIndex == 1) this.$refs.scroller.reset({ top: 0 });

this.$refs.scroller.donePullup();
this.$refs.scroller.donePulldown();
},
fial => {

this.$refs.scroller.donePullup();
this.$refs.scroller.donePulldown();
}
);
},

requestGetMaterialGoods() {
this.$request
.loading(
this,
homeApi.GetMaterialGoods,
"post",
{
PromoterId: "",
MaterialId: this.materialId,
PageSize: 20,
PageIndex: this.pageIndex
},
""
)
.then(
success => {
this.loadingShow = true;
this.loadingEnd = success.data.code == 0 ? true : false;
if (success.data.code == 0) {
if (this.pageIndex == 1) {

this.commodityList = [];
}

this.commodityList = this.commodityList.concat(success.data.data);
}

if (this.pageIndex == 1) this.$refs.scroller.reset({ top: 0 });

this.$refs.scroller.donePullup();
this.$refs.scroller.donePulldown();
},
fial => {

this.$refs.scroller.donePullup();
this.$refs.scroller.donePulldown();
}
);
}
}
};



